<template>
  <div>
    <div class="choice-role" v-if="Object.values(choiceProduct).length">
      <template v-for="(item, index) in choiceProduct">
        <div
          :key="index"
          class="choice-product"
          :class="item.uniqueId === choiceUniqueId ? 'cur-product' : ''"
          @click="choiceUniqueId = item.uniqueId"
        >
          <span class="product">{{item.cname}}</span>
          <template v-for="(role, roleIndex) in item.role">
            <el-tag
              v-if="role.choice"
              :key="`${index}_${roleIndex}`"
              style="margin-right: 5px"
              closable
              @close.stop="role.choice = false"
              type="success"
            >{{ role.policyName }}</el-tag>
          </template>
          <span class="close el-icon-close" @click.stop="$delete(choiceProduct, index)"></span>
        </div>
      </template>
      <div class="clear"></div>
    </div>
    <div class="product-role">
      <div class="item">
        <div class="item-title">产品列表</div>
        <el-scrollbar style="height: calc(100% - 60px);margin-top: 10px">
          <div
            v-for="(item, index) in productList"
            :key="index"
            class="product-list"
            :class="item.uniqueId === choiceUniqueId ? 'cur-product' : ''"
            @click="updateProduct(item)"
          >{{item.cname}}</div>
        </el-scrollbar>
      </div>
      <div class="item">
        <div class="item-title">角色列表 <span v-if="(choiceProduct[choiceUniqueId] || {}).cname">({{(choiceProduct[choiceUniqueId] || {}).cname}})</span></div>
        <el-scrollbar style="height: calc(100% - 60px);margin-top: 10px">
          <div style="padding: 10px">
            <el-empty description="该产品无角色" v-if="!getRole.length && (choiceProduct[choiceUniqueId] || {}).cname"></el-empty>
            <el-button
              plain
              v-for="(item, index) in getRole"
              :key="index"
              :type="item.choice ? 'primary' : 'default'"
              @click="clickRole(item)"
              style="margin-bottom: 10px"
            >{{item.policyName}}</el-button>
          </div>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>

import fetch from 'cseed-frame/services/_fetch'
export default {
  components: {},
  props: {
    product: {
      type: Array,
      default: () => {
        return []
      }
    },
    menu: {
      type: Object,
      default: () => {
        return {}
      }
    },
    camPolicy: {
      type: [Array, Object],
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      api: {},
      choiceProduct: {},
      choiceRole: [],
      productList: [],
      choiceUniqueId: ''
    }
  },
  computed: {
    getRole () {
      return (this.choiceProduct[this.choiceUniqueId] || {}).role || []
    }
  },
  watch: {
    product: {
      handler (newName) {
        this.productList = JSON.parse(JSON.stringify(newName))
      },
      deep: true
    },
    camPolicy: {
      handler (newData) {
        this.choiceProduct = JSON.parse(JSON.stringify(newData))
      },
      deep: true
    }
  },
  methods: {
    roleChoiceLength (data) {
      return (data.role || []).filter(item => {
        return item.choice
      }).lenght
    },
    getData () {
      const productcamPolicyDtoList = Object.values(JSON.parse(JSON.stringify(this.choiceProduct))).map(item => {
        const data = {
          productUniqueId: item.uniqueId,
          camPolicyList: []
        }
        item.role.forEach(role => {
          if (role.choice) {
            data.camPolicyList.push({
              policyCode: role.policyCode,
              policyUniqueId: role.policyUniqueId,
              id: role.id
            })
          }
        })

        return data
      })
      return productcamPolicyDtoList
    },
    clickRole (data) {
      this.$set(data, 'choice', !data.choice)
    },
    updateProduct (data) {
      const uniqueId = data.uniqueId
      // this.$set(data, 'choice', !data.choice)
      if (!this.choiceProduct[uniqueId]) {
        if (!data.role) {
          fetch({
            url: this.api['listByProdUniqueld'][0],
            method: this.api['listByProdUniqueld'][1],
            params: {
              productUniqueId: uniqueId
            }
          }).then(({ data: { list } }) => {
            this.$set(data, 'role', list)
            this.$set(this.choiceProduct, uniqueId, {
              cname: data.cname,
              uniqueId: uniqueId,
              role: list
            })
          })
        } else {
          this.$set(this.choiceProduct, uniqueId, {
            cname: data.cname,
            uniqueId: uniqueId,
            role: data.role
          })
        }
      }
      this.choiceUniqueId = uniqueId
    }
  },
  created () {
    this.api = JSON.parse(this.menu.apiJson)
  }
}
</script>

<style lang="stylus" scoped>
.choice-role
  padding-bottom 10px
  .choice-product
    border 1px solid #DCDFE6
    float left
    margin 0 10px 10px 0
    padding 0 10px 0 5px
    border-radius 5px
  .cur-product
    border-color #b3d8ff
    background #ecf5ff
    color #409EFF
  .product
    padding-right 8px
    font-size 12px
    cursor pointer
  .close
    cursor pointer
  .clear
    clear both
.product-role
  height 400px
  max-width 1000px
  .item
    width calc(50% - 20px)
    height 400px
    float left
    border-radius 5px
    border 1px solid #DCDFE6
    .item-title
      height 40px
      line-height 40px
      padding 0 10px
      font-size 14px
      border-bottom 1px solid #DCDFE6
    .product-list
      height 26px
      line-height 26px
      padding 0 15px
      font-size 14px
      color #666
      cursor pointer
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
    .cur-product
      color #409EFF
  .item:first-child
    margin-right 20px
</style>
