<template>
  <div>
    <el-form
      label-width="150px"
      :model="formData"
      :rules="rules"
      ref="userForm"
    >
      <el-form-item label="用户类型">
        <el-select v-model="formData.pin" :disabled="isEdit">
          <el-option
            v-for="item in userEditList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :label="formData.pin === 1 ? '用户账户' : '域账号'"
        prop="userName"
        :rules="formData.pin === 1 ? userName : mainUserName"
      >
        <el-input
          :disabled="isEdit"
          placeholder="请输入用户账户"
          v-model="formData.userName"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="用户名称" prop="userNickname">
        <el-input placeholder="请输入用户名称" v-model="formData.userNickname" autocomplete="off"></el-input>
      </el-form-item>
      <!-- <el-form-item v-if="isEdit" label="手机号">{{formData.mobilePhone}}</el-form-item> -->
      <el-form-item label="手机号" prop="mobilePhone">
        <el-input
          placeholder="请输入手机号"
          maxlength="11"
          v-model="formData.mobilePhone"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="所属组织" prop="depUniqueId">
        <el-cascader
          :options="depUniqueOptions"
          v-model="formData.depUniqueId"
          :show-all-levels="false"
          :props="{
            checkStrictly: true,
            value: 'uniqueId',
            label: 'departmentName'
          }"
        ></el-cascader>
      </el-form-item>
      <template v-if="formData.pin === 1">
      <el-form-item label="邮箱" prop="mailbox">
        <el-input placeholder="请输入邮箱" v-model="formData.mailbox" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password" :rules="passwordRules">
        <input type="text" hidden/>
        <input type="password" hidden autocomplete="new-password"/>
        <el-input
          placeholder="请输入密码"
          v-model="formData.password"
          autocomplete="off"
          :type="isShowPwd ? 'string' : 'password'"
        >
          <el-button
            slot="append"
            @click="isShowPwd = !isShowPwd"
            icon="el-icon-view"
            :style="{
              fontSize: '14px',
              color: isShowPwd ? '#409EFF' : '#ccc'
            }"
          >
          </el-button>
        </el-input>
        <!-- <span class="tips">用户密码至少8位且必需要有数字+特殊字符+字母</span> -->
      </el-form-item>
      <el-form-item label="状态">
        <el-switch
          v-model="formData.state"
          :active-value="1"
          :inactive-value="2">
        </el-switch>
      </el-form-item>
      </template>
      <el-form-item label="备注">
        <el-input
          type="textarea"
          :rows="5"
          placeholder="请输入内容"
          v-model="formData.remark"
        ></el-input>
      </el-form-item>
      <el-form-item
        label="产品角色"
      >
        <product-role
          :product="productList"
          :camPolicy="productObj"
          :menu="menu"
          ref="role"
        ></product-role>
      </el-form-item>
      <el-form-item>
        <el-button @click="$emit('go-back')">取消</el-button>
        <el-button type="primary" @click="save">保存并返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Base64 from 'crypto-js/enc-base64'
import Utf8 from 'crypto-js/enc-utf8'
import productRole from './user-components/product-role'
import { genApiService } from 'cseed-frame/services/_common'
import { parseMenu } from 'cseed-frame/utils/_common'
export default {
  components: {
    productRole
  },
  inject: ['baseIndex'],
  props: ['mode', 'id', 'menuId', 'menu'],
  data () {
    return {
      password1: '',
      isShowPwd: false,
      formData: {
        pin: 1,
        state: 1
      },
      userEditList: [
        { label: '普通用户', value: 1 },
        { label: '域账号用户', value: 3 }
      ],
      passwordRules: [
        { required: this.mode === 'create', message: '请填写密码', trigger: ['blur', 'change'] },
        // eslint-disable-next-line no-useless-escape
        { pattern: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[`~!@#$%^&*()_+<>?:"{},.\/\\;'[\]])[A-Za-z\d`~!@#$%^&*()_+<>?:"{},.\/\\;'[\]]{8,}$/, message: '至少8位且必需要有数字+特殊字符+字母', trigger: ['blur', 'change'] },
        { max: 16, message: '密码最长16位', trigger: ['blur', 'change'] }
      ],
      mainUserName: [
        { required: true, message: '请填写域账户', trigger: ['blur', 'change'] },
        { max: 100, message: '域账户最长100位', trigger: ['blur', 'change'] },
        { type: 'email', message: '请填写正确域账户', trigger: 'blur' }
      ],
      userName: [
        { required: true, message: '请填写用户账户', trigger: ['blur', 'change'] },
        { pattern: /^[a-zA-Z0-9]+$/, message: '仅支持26个英文字母和数字0~9；最多32字符', trigger: ['blur', 'change'] },
        { max: 32, message: '用户账户最长32位', trigger: ['blur', 'change'] }
      ],
      rules: {
        productUniqueId: [
          { required: true, message: '请选择产品', trigger: 'change' }
        ],
        userNickname: [
          { required: true, message: '请填写用户名', trigger: ['blur', 'change'] },
          { max: 32, message: '用户名最长32位', trigger: ['blur', 'change'] }
        ],
        mobilePhone: [
          { required: true, message: '请填写手机号码', trigger: 'blur' },
          {
            trigger: 'blur',
            validator: (rule, value, callback) => {
              if (!this.$cUtils.validate.isMobile(value)) {
                callback(new Error('请输入正确的手机号码'))
              } else {
                callback()
              }
            }
          }
        ],
        depUniqueId: [
          {
            trigger: 'change',
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error('请选择所属组织！'))
              }
            }
          }
          // { type: 'array', required: true, message: '请选择所属组织', trigger: 'change' }
        ],
        mailbox: [{ type: 'email', message: this.$t('message.emailIncorrect'), trigger: 'blur' }]
      },
      apiServer: undefined,
      productList: [],
      productObj: {},
      depUniqueOptions: []
    }
  },
  computed: {
    isEdit () {
      return this.mode === 'edit'
    }
  },
  methods: {
    depTreeList () {
      this.apiServer('depTreeList')().then(res => {
        this.depUniqueOptions = res
      })
    },
    save () {
      this.$refs.userForm.validate((valid) => {
        if (valid) {
          const copyData = JSON.parse(JSON.stringify(this.formData))
          let password
          if (this.formData.password) {
            password = Base64.stringify(Utf8.parse(this.formData.password))
          }
          const depUniqueId = Array.isArray(copyData.depUniqueId) ? (copyData.depUniqueId?.pop() || '0') : copyData.depUniqueId
          if (this.formData.mobilePhone && this.mode === 'edit') {
            this.formData.mobilePhone = undefined
          }
          this.apiServer(this.mode === 'edit' ? 'update' : this.formData.pin === 3 ? 'addaAreaUser' : 'add')({
            data: {
              ...copyData,
              password,
              depUniqueId,
              productCamPolicyDtoList: this.$refs.role.getData()
            }
          }).then(() => {
            this.$emit('upsert-end')
          })
        }
      })
    },
    async listByProdUniqueld (uniqueId) {
      let dataRes = await this.apiServer('listByProdUniqueld')({
        params: {
          productUniqueId: uniqueId
        }
      })
      return dataRes
    }
  },
  mounted () {
    // this.isPwdReadonly = true // 防止密码自定填充
    // setTimeout(() => {
    //   this.isPwdReadonly = false
    // }, 1000)
  },
  created () {
    const { api } = parseMenu(this)
    this.apiServer = genApiService(api)
    this.apiServer('getProduct')({
      params: {
        page: 1,
        pageSize: 9999
      }
    }).then(({ list }) => {
      this.productList = list
      const obj = {}
      list.forEach(item => {
        this.$set(obj, item.uniqueId, item)
      })
      if (this.mode === 'edit') {
        this.apiServer('detail')({
          params: {
            id: this.id
          }
        }).then(data => {
          const productCamPolicy = JSON.parse(JSON.stringify(data.productCamPolicyDtoList))
          this.$delete(data, 'productCamPolicyDtoList')
          this.$set(data, 'pin', data.pin !== 3 ? 1 : 3)
          productCamPolicy.forEach(item => {
            const roleIdList = item.camPolicyList.map((role) => role.id)
            const uniqueId = item.productUniqueId
            if (obj[uniqueId]) {
              this.$set(this.productObj, uniqueId, {
                cname: obj[uniqueId].cname,
                uniqueId
              })
              this.listByProdUniqueld(uniqueId).then(({ list }) => {
                const roleList = list.map(role => {
                  return {
                    choice: roleIdList.includes(role.id),
                    ...role
                  }
                })
                this.$set(this.productObj[uniqueId], 'role', roleList)
              })
            }
          })
          this.formData = data
        })
      }
    })
    this.depTreeList()
  }
}
</script>

<style lang="stylus" scoped>
</style>
