import { getUserErrorCount, clearUserErrorCount } from 'cseed-frame/services/_sys'

export default {
  clearLoginError ({ row }) {
    const params = { userName: row.userName }
    getUserErrorCount(params).then(({ data }) => {
      if (!data) {
        return this.$message.info(`${row.userName} 累计登录失败次数已经为 ${data},不需要重置！`)
      }
      this.$confirm(`${row.userName} 累计登录失败次数为${data}, 是否解锁（将该用户的登录失败次数重置为0）?`, '提示', {
        type: 'warning'
      }).then(() => {
        clearUserErrorCount(params, '解锁成功').then(() => {
          debugger
          this.clearSelected() // ssssssssssssssss
        })
      }).catch(() => {})
    })
  }
}
